.container {
    margin: 0 auto;
    max-width: 1440px;
    background-color: rgb(255, 255, 255);
    padding: 64px 20px 64px 20px;
}

@media screen and (min-width: 768px) {
    .container {
        padding: 64px 94px 100px 94px;
    }
}

@media screen and (min-width: 1200px) {
    .container {
        padding: 100px 128px 118px 128px;
    }
}

.item {
    display: flex;
        border: 1px solid;
        border-color: #ebe8e4;
        border-radius: 8px;
        width: 490px;
        padding: 25px;

}



@media screen and (min-width: 768px) {
    .item {
display: flex;
    }
   
}

